<template>
  <div class="view">
    <div class="btns">
      <el-button @click="add" icon="el-icon-plus" plain type="primary"
        >新增</el-button
      >
      <!-- <el-button @click="view" icon="el-icon-view" plain type="success">查看</el-button> -->
      <el-button @click="edit" icon="el-icon-edit" plain type="info"
        >编辑</el-button
      >
      <el-button @click="remove" icon="el-icon-remove" plain type="danger"
        >删除</el-button
      >
    </div>

    <el-table
      :data="posts.slice((currentPage - 1) * pageSize, currentPage * pageSize)"
      @current-change="handleCurrentChange"
      highlight-current-row
      style="width: 100%"
      v-loading="loading"
    >
      <el-table-column label="序号" type="index" width="100"></el-table-column>
      <el-table-column
        :key="index"
        :label="item.title"
        :prop="item.prop"
        v-for="(item, index) in columns"
      >
        <template slot-scope="scope">
          <span v-if="item.prop === 'date'">
            {{ scope.row[item.prop] | dateFormat }}
          </span>
          <span v-else>{{ scope.row[item.prop] }}</span>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      :current-page.sync="currentPage"
      :page-size.sync="pageSize"
      :total="totalCount"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      @page-change="handlePageChange"
      @next-click="handleNext"
      @prev-click="handlePrev"
      background
      :page-sizes="[10, 20, 30, 50, 100]"
      layout="total, sizes, prev, pager, next, jumper"
    ></el-pagination>
  </div>
</template>

<script>
import { githubApi } from "@/utils/github-api";
import { base64ToUtf8 } from "@/utils/common";
export default {
  data() {
    return {
      postList: [],
      columns: [
        {
          title: "标题",
          prop: "name",
          width: 120,
        },
        {
          title: "简介",
          prop: "description",
          width: 120,
        },
        {
          title: "分类",
          prop: "category",
          width: 120,
        },
        {
          title: "作者",
          prop: "author",
          width: 120,
        },
        {
          title: "路径",
          prop: "path",
          width: 120,
        },
        {
          title: "创建日期",
          prop: "date",

          width: 120,
        },
        {
          title: "是否推荐",
          prop: "isRecommend",
          width: 120,
        },
        {
          title: "是否发布",
          prop: "isPublish",
          width: 120,
        },
      ],
      currentPage: 1,
      pageSize: 10,
      totalCount: 0,
      currentId: null,
      loading: true,
    };
  },
  mounted() {
    this.getData();
  },
  methods: {
    handleSizeChange(val) {
      this.getData();
    },
    handleSelectionChange(val) {
      this.selectedFile = (val.length && val[0]) || null;
    },
    handleCurrentChange(val) {
      this.selectedFile = val;
    },
    async getData() {
      this.loading = true;
      const file = await githubApi.getFile(`db/_post/postList.json`);
      this.loading = false;
      if (!file) {
        this.$message.error("No data");
        return;
      }
      this.postList = JSON.parse(base64ToUtf8(file.content));
      this.postList.sha = file.sha;
    },
    handlePrev() {
      if (this.currentPage > 1) {
        this.currentPage--;
      }
    },
    handleNext() {
      if (this.totalCount / this.pageSize > this.currentPage) {
        this.currentPage++;
      }
    },
    handlePageChange(page) {
      this.currentPage = page;
    },
    add() {
      this.$router.push({
        path: `/admin/list/add`,
      });
    },
    edit() {
      if (this.selectedFile) {
        this.$router.push({
          path: `/admin/list/edit`,
          query: {
            ...this.selectedFile,
          },
        });
      }
    },
    async remove() {
      if (!this.selectedFile) return;
      if (!confirm("确定删除？")) return;
      let res = await githubApi.removeFile(this.selectedFile.path);
      if (res.status !== "OK") {
        this.$message.error(res.msg);
      }
      this.postList.data = this.postList.data.filter((item) => {
        return item.path != this.selectedFile.path;
      });
      this.$loading.show();
      res = await githubApi.updateFile(
        this.postList.path,
        this.postList.sha,
        JSON.stringify(this.postList)
      );
      this.$loading.hide();
      if (res.status !== "OK") {
        this.$message.error(res.msg);
      } else {
        this.$message(res.msg);
      }
      await this.getData();
    },
  },
  computed: {
    posts() {
      let list = [];
      if (this.postList.data) {
        const postArr = this.postList.data;
        postArr.forEach((item) => {
          list.push(item);
        });
      }
      this.totalCount = list.length;
      return list;
    },
  },
};
</script>

<style lang="scss" scoped>
.view {
  height: calc(100% - 20px);
  padding-bottom: 40px;
  position: relative;

  .btns {
    text-align: left;
    height: 50px;
    line-height: 50px;

    .el-button {
      padding: 5px;
    }
  }

  .el-table {
    height: calc(100% - 50px);
  }

  .el-pagination {
    position: absolute;
    bottom: 0;
    left: 0;
  }
}
</style>
