import axios from "@/ajax/request";
import { getToken } from "@/utils/cookie-util";
import { to, utf8ToBase64, base64ToUtf8 } from "@/utils/common";

const { githubOauthOptions } = require("@/settings");
const { baseApiURL, owner, repo, branch } = githubOauthOptions;

const baseRepoURL = `${baseApiURL}/repos/${owner}/${repo}/contents`;
const baseRawURL = `https://raw.githubusercontent.com/${owner}/${repo}/${branch}`;
const githubApi = {
  getFileSha: async (fileName) => {
    let token = getToken();
    let response = await fetch(`${baseRepoURL}/${fileName}`, {
      method: 'GET',
      headers: {
          'Accept': 'application/vnd.github.v3+json',
          'Authorization': `token ${token}`
      },
      mode:'cors'
    });
    let data = await response.json();
    if (!data) {
      return "";
    }
    if (!data.sha) {
      return "";
    }
    return data.sha;
  },

  getFile: async (fileName) => {
    let token = getToken()
    let response = await fetch(`${baseRepoURL}/${fileName}`, {
      method: 'GET',
      headers: {
          'Accept': 'application/vnd.github.v3+json',
          'Authorization': `token ${token}`
      },
      mode:'cors'
    });
    let data = await response.json();
    if (!data) {
      return null;
    }
    return data;
  },

  getTextRawFile: async (fileName) => {
    let response = await fetch(`${baseRawURL}/${fileName}`)
    if (!response.ok) {
      return null;
    }
    let data = await response.text();
    return data;
  },

  getJsonRawFile: async (fileName) => {
    let response = await fetch(`${baseRawURL}/${fileName}`)
    if (!response.ok) {
      return null;
    }
    let data = await response.json();
    return data;

  },

  addFile: async (fileName, content) => {
    content = utf8ToBase64(content);
    let body = {
      message: 'commit message',
      committer: {
        name: 'Yinbe',
        email: 'Yinbe@github.com'
      },
      content: content
    }
    let token = getToken()
    let header = {
      'Accept': 'application/vnd.github.v3+json',
      'Authorization': `token ${token}`
    }
    let response = await fetch(`${baseRepoURL}/${fileName}`, {
      method: 'PUT',
      headers: header,
      body: JSON.stringify(body),
      mode:'cors',
    });
    if (!response.ok) {
      return {
        status: "Fail",
        msg: `添加失败： `,
      };
    }
    return {
      status: "OK",
      msg: `添加成功： ${fileName}`,
    };
  },

  removeFile: async (fileName) => {
    const sha = await githubApi.getFileSha(fileName);
    if (!sha) {
      return {
        status: "Fail",
        msg: `不存在 ${fileName}`,
      };
    }
    let token = getToken()
    let body = {
      owner: owner,
      repo: repo,
      path: fileName,
      message: 'commit message',
      committer: {
        name: 'Yinbe',
        email: 'Yinbe@github.com'
      },
      sha: sha
    }
    let response = await fetch(`${baseRepoURL}/${fileName}`, {
      method: 'DELETE',
      headers: {
          'Accept': 'application/vnd.github.v3+json',
          'Authorization': `token ${token}`
      },
      body: JSON.stringify(body),
      mode:'cors'
    });
    if (!response.ok) {
      return {
        status: "Fail",
        msg: `删除失败!`,
      };
    }
    return {
      status: "OK",
      msg: `删除成功： ${fileName}`,
    };
  },

  updateFile: async (fileName, sha, content) => {
    content = utf8ToBase64(content);
    let body = {
      message: 'commit message',
      committer: {
        name: 'Yinbe',
        email: 'Yinbe@github.com'
      },
      content: content,
      sha: sha
    }
    let token = getToken()
    let header = {
      'Accept': 'application/vnd.github.v3+json',
      'Authorization': `token ${token}`
    }
    let response = await fetch(`${baseRepoURL}/${fileName}`, {
      method: 'PUT',
      headers: header,
      body: JSON.stringify(body),
      mode:'cors',
    });
    if (!response.ok) {
      return {
        status: "Fail",
        msg: `更新失败`,
      };
    }
    return {
      status: "OK",
      msg: `更新成功： ${fileName}`,
    };
  },

  upLoadMediaFile: async (fileName, content) => {
    content = utf8ToBase64(content);
    let body = {
      message: 'commit message',
      committer: {
        name: 'Yinbe',
        email: 'Yinbe@github.com'
      },
      content: content
    }
    let token = getToken()
    let header = {
      'Accept': 'application/vnd.github.v3+json',
      'Authorization': `token ${token}`
    }
    let response = await fetch(`${baseRepoURL}/${fileName}`, {
      method: 'PUT',
      headers: header,
      body: JSON.stringify(body),
      mode:'cors',
    });
    if (!response.ok) {
      return {
        status: "Fail",
        msg: `添加失败： `,
      };
    }
    return {
      status: "OK",
      msg: `添加成功： ${fileName}`,
    };
  },

  checkRepo: async (_) => {
    const params = { access_token: getToken() };
    const [err, res] = await to(axios.get(baseRepoURL, { params }));
    if (err) {
      return false;
    }
    return true;
  },

  forkRepo: async (_) => {
    const data = { access_token: getToken() };
    const [err, res] = await to(
      axios.post(`${baseApiURL}/repos/zclzone/gitee-db/forks`, { data })
    );
    if (err) {
      return {
        status: "Fail",
      };
    }
    return {
      status: "OK",
    };
  },
};

export { githubApi };
